body {
  background: #f9f9f9;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

h1 {
  text-align: center;
  padding: 1rem;
}

form {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  padding: 1rem;
  display: flex;
}

select, input[type="text"], button {
  padding: .5rem;
  font-size: 1rem;
}

.task-board {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  padding: 1rem;
  display: flex;
}

.task-column {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: .5rem;
  width: 300px;
  min-height: 100px;
  padding: 1rem;
  box-shadow: 0 2px 6px #0000000d;
}

.task-column h2 {
  text-align: center;
  margin-top: 0;
}

.task-card {
  background: #f1f1f1;
  border-radius: .5rem;
  margin-bottom: 1rem;
  padding: .75rem;
  box-shadow: 0 1px 4px #0000001a;
}

.task-card h3 {
  margin: 0 0 .5rem;
}

.task-card button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: .25rem;
  margin-top: .5rem;
  padding: .4rem .8rem;
}

.task-card button:hover {
  background-color: #0056b3;
}

.task-card select {
  width: 100%;
  margin-top: .5rem;
}
